
.layout {
    background: black;
}
.main-layout{
    display: flex;
    justify-content: space-between;
    align-items: center
}
.logo {
    position: absolute;
    top: 0;
}
.logo h1{
    
 /* font-family: ; */
    color: white;
}
.side-left{
    width: 200px;
    padding-left: 20px;
}

.center{
    width: 250px;
    position: relative;
}


.right-side{
    width: 200px;
}

.side-left-img , .center-image , .right-img , .side-left-img img , .center-image img , .right-img img {
    width: 100%;
}

.next-button button {
    background: transparent;
    border: none;
   align-items: center;
   justify-content: center;
   color: white;
}

.arrow-icon{
    width: 20px;
}

.arrow-icon img{
width: 100%;
}
.next-button {
    position: absolute;
    bottom: 0;
    right: -100px;
    gap: 12px;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    display: flex;
    width: 101px;
    height: 40px;
    background: rgba(32, 32, 32, 1);
}

@media screen and (max-width:670px) {
    .main-layout{
        flex-direction: column;
    }
    .side-left {
        /* width: 200px; */
        padding-left: 0px;
    }

    .next-button {
        position:static;
        margin: auto;
        margin-bottom: 12px;
    }
}